#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}



@media screen and (min-width: 1080px) {
    .skillsGrid{
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 50px;
      grid-auto-rows: auto;
      height: auto;
    }
  .skillsGrid img {
    height: 150px;
    width: 150px;
  }
  .skills{
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  }


  /* Medium Screens min width 641px change to 4 columns */
@media screen and (min-width: 641px) {
    .skillsGrid{
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 30px;
      grid-auto-rows: auto;
      height: auto;
    }
  .skillsGrid img {
    height: 100px;
    width: 100px;
  }
  .skills{
    background-color: bl;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
  }

  /* Small Screens max width 640px */
@media screen and (max-width: 640px) {
    .skillsGrid{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
      grid-auto-rows: auto;
      height: auto;
    }
  .skillsGrid img {
    height: 75px;
    width: 75px;
  }
  .skills{
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    justify-content: center;
    align-items: center;
  }
  }